import React, {useState, Fragment} from 'react';

import MenuIcon from '@material-ui/icons/Menu';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Box from '@material-ui/core/Box';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import FacebookIcon from '@material-ui/icons/Facebook'
import TwitterIcon from '@material-ui/icons/Twitter'
import { makeStyles } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper,
    },
    list: {
      width: 250,
      backgroundColor: "#FFFFFF"
    },
    fullList: {
      width: 'auto',
    },
    title: {
        color: "#082D68",
        textAlign: "center",
        fontWeight: "bold"
    },
    facebookIcon: {
        color: theme.palette.common.facebook,
    },
    twitterIcon: {
        color: theme.palette.common.twitter,
    },
    shareContainer: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        margin: 'auto 0 0 0',
        padding: theme.spacing(1),
    },
  }));

export default function SeasonDetails(){
    const classes = useStyles();

    const [sidebar, setSidebar] = useState(false);

    const toggleDrawer = (open) => (event) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
          return;
        }
    
        setSidebar(open);
      };
    return (
    <Fragment>
        <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={toggleDrawer(true)}
            edge="start"
        >
            <MenuIcon />
        </IconButton>        
        <SwipeableDrawer
            open={sidebar}
            onClose={toggleDrawer(false)}
            onOpen={toggleDrawer(true)}
            >
            <div
                className={classes.list}
                role="presentation"
                onClick={toggleDrawer(false)}
                onKeyDown={toggleDrawer(false)}
            >
            <Typography variant="h6" className={classes.title}>
                ArchivioRossoBlu
            </Typography>
            <List>
                <ListItem button key="Stagioni">
                    <ListItemIcon><SportsSoccerIcon /></ListItemIcon>
                    <ListItemText primary="Stagioni" />
                </ListItem>
                <ListItem button key="Statistiche">
                    <ListItemIcon><EqualizerIcon /></ListItemIcon>
                    <ListItemText primary="Statistiche" />
                </ListItem>
            </List>
            <Divider />
            <Box className={classes.shareContainer}>
                <IconButton href="https://www.facebook.com/archiviorossoblu.it" target="_blank">
                    <FacebookIcon className={classes.facebookIcon} />
                </IconButton>
                <IconButton href="https://twitter.com/archiviorossobl" target="_blank">
                    <TwitterIcon className={classes.twitterIcon} />
                </IconButton>
            </Box>
            </div>
        </SwipeableDrawer>
    </Fragment>
    )
}